@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
    padding: 3rem 0;
    border-bottom: 0.5rem solid v.$primario;
    &__grid {
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    &__copyright {
        text-align: center;
        font-size: 2.4rem;
    }
    .insta {
        display: flex;
        justify-content: center;
    }
    .jakes-anchor {
        text-decoration: underline;
        color: v.$primario;
    }
    .sociales {
        margin: 5rem 0;
        display: flex;

        @include m.tablet {
            margin: 0;
        }

        &__enlace {
            &::before {
                content: "";
                padding: 2rem;
                display: inline-block;
            }
            &[href*="facebook.com"]::before {
                background-image: url(../../../../build/img/icono_facebook.svg);
            }
            &[href*="twitter.com"]::before {
                background-image: url(../../../../build/img/icono_twitter.svg);
            }
            &[href*="instagram.com"]::before {
                background-image: url(../../../../build/img/icono_instagram.svg);
            }
            &[href*="pinterest.com"]::before {
                background-image: url(../../../../build/img/icono_pinterest.svg);
            }
            &[href*="youtube.com"]::before {
                background-image: url(../../../../build/img/icono_youtube.svg);
            }
            &[href*="tiktok.com"]::before {
                background-image: url(../../../../build/img/icono_tiktok.svg);
            }
        }
        &__accesible {
            width: 1px;
            height: 1px;
            padding: 0;
            margin: -1px;
            overflow: hidden;
            clip: rect(0, 0, 0, 0);
            border: 0;
            display: block;
        }
    }
}
